export default {
	data() {
		return {
			// id: this.$route.query.id,
			// total: this.$route.query.total,
			id:'',
			price: '',
			radio: "",
			content: "",
			items: [{
					value: '3',
					short: '多拍了'
				},
				{
					value: '2',
					short: '不想买了',
					checked: 'true'
				},
				{
					value: '1',
					short: '其他'
				}
			],
			current: -1
		};
	},
	components: {},
	onLoad(options) {
		this.id = options.id
		const cache = uni.getStorageSync('price')
		this.price = cache
	},
	methods: {
		radioChange(e){
			this.radio = e.detail.value
		},
		sub() {
			const that = this
			if (this.radio < 1) {
				this.$api.msg('未选择退款原因')
				// this.$toast('未选择退款原因')
				return;
			}

			let data = {
				order_id:this.id,
				radio:this.radio,
				content:this.content,
				goods_id:0
			}
			this.$api.http.post('order/user/tui_kuan', data).then(res => {
				if(res.data == 1){ 
					this.$api.msg('操作成功')
					setTimeout(()=>{
						uni.navigateBack({
						})
					},1000)
				}else{
					this.$api.msg(res.msg)
				}
			});
		}
	},
	mounted() {}
};
